body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}

.navigation__list {
  display: flex;
  justify-content: flex-end;

  list-style: none;
  padding: 0;
  margin: 0;

  box-shadow: 0 0 4px #d3d3d3;
}

.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 0 auto;
  max-width: 1300px;
}

.navigation__link {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  min-height: 40px;

  color: #000;
  background-color: #fff;

  text-decoration: none;
}

.navigation__link:hover {
  background-color: rgb(235, 235, 235);
}

.active {
  color: #fff;
  background-color: #0000ff;
}

.active:hover {
  color: #fff;
  background-color: #0000ff;
}

.phone__galery {
  width: 70px;
  border: 1px solid black;
  margin-right: 10px;
  padding: 10px;
  background-color: #fff;

  cursor: pointer;
  transition: all 0.6s;
}

.phone__preview {
  width: 100%;
}

.phone__card-list {
  display: flex;
  flex-wrap: wrap;

  list-style: none;
  margin: 0;
  padding: 0;
  max-width: 1300px;
}

.phone__card-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  box-sizing: border-box;
  margin: 10px;
  padding: 20px;
  width: 240px;

  box-shadow: 0 0 10px #d3d3d3;
}

.backet__card-list {
  display: flex;
  flex-wrap: wrap;

  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}

.backet__card-item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;

  box-sizing: border-box;
  margin: 10px 0;
  padding: 20px;
  width: 100%;

  box-shadow: 0 0 10px #d3d3d3;
}

.basket__preview-name-container {
  display: flex;
  align-items: center;
}

.backet__card-image {
  width: 60px;
  margin-right: 20px;
}

.backet__card-image img {
  width: 100%;
}

.phone__buttons {
  display: flex;
  justify-content: space-between;

}

.button {
  padding: 15px 20px;
  color: #fff;
  background-color: #3232e2;
  border: none;

  text-transform: uppercase;
}

.button--basket {
  min-width: 40px;
  margin-left: 20px;
}

.button:hover {
  background-color: #2222db;
  cursor: pointer;
}

.basket__controls {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
